.slider {
  overflow: hidden;

  @include onNotebook {
    display: none;
  }

  &__content {
    display: flex;
    margin-bottom: 50px;
    transition: transform 0.3s;
  }

  &__item {
    flex-shrink: 0;
    width: 100%;
    transform: translateX(0);
  }

  &__nav {
    display: flex;
    width: 200px;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    @include onTablet {
      width: 100px;
    }

    @include onMobile {
      margin: auto;
    }

    @include onTablet {
      margin-bottom: 123px;
    }

    @include onNotebook {
      display: none;
    }
  }

  &__button {
    width: 55px;
    height: 14px;
    background-repeat: no-repeat;
    background-color: $features-container;
    border: none;
    cursor: pointer;
  }

  &__buttons {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }

  &__page {
    position: relative;
    top: -3px;
    font-size: 16px;
    line-height: 24px;
  }

  &__total-pages {
    font-size: 11px;
    line-height: 13px;
    color: $article-title;
  }

  &__button-n,
  &__button-p {
    transition: filter 0.3s;

    &:hover {
      filter: invert(26%)
        sepia(62%)
        saturate(2528%)
        hue-rotate(160deg)
        brightness(92%)
        contrast(91%)
      ;
    }
  }
}
